.currency-block
    width: 100%
    display: grid
    column-gap: 1rem
    grid-template-columns: 1fr auto
    align-items: center

    &:not(:first-child)
        margin-top: 0.8rem

.currency-block__inputs
    width: 100%
    padding: 0.4rem 1rem

    border-radius: 0.8rem
    background-color: $tertiary-color

.currency-block__inputs-wrapper
    display: flex
    align-items: center
    justify-content: space-between

    font-size: 2.2rem

.currency-block__amount
    width: 16.5rem
    height: 3rem
    padding: 0 0.6rem

    font-size: 2.2rem
    text-align: right

    color: $secondary-color
    border: none
    border-radius: 0.8rem

    &:focus-visible
        outline: none

.currency-block__error
    margin-top: 0.2rem
    margin-left: 28.6rem

    font-size: 1.1rem

.currency-block select
    width: 7.4rem
    height: 3rem
    padding-left: 0.2rem

    font-size: 2.2rem

    color: $secondary-color
    border: none
    border-radius: 0.8rem

    &:focus-visible
        outline: none

@media (max-width: 768px)
    .currency-block
        column-gap: 0.8rem

    .currency-block__inputs-wrapper>span
        display: none

    .currency-block__inputs
        width: 100%
        padding: 0.4rem 0.8rem

        border-radius: 0.8rem

    .currency-block__amount
        width: 16rem
        height: 2.8rem
        padding: 0 0.6rem

        font-size: 2rem
        text-align: right

        color: $secondary-color
        border: none
        border-radius: 0.8rem



    .currency-block__error
        margin-top: 0.2rem
        margin-left: 10.6rem

        font-size: 1rem

    .currency-block select
        width: 7rem
        height: 2.8rem
        padding-left: 0.2rem

        font-size: 2rem

        color: $secondary-color
        border: none
        border-radius: 0.8rem

        &:focus-visible
            outline: none